.projects {
  padding-bottom: 2rem;
  overflow-x: hidden;

  &-bio-image {
    height: 30vh;
    width: 100%;
    background: linear-gradient(
        to right,
        rgba($primaryColor, 0.9),
        rgba($primaryColor, 0.3)
      ),
      url('../assets/model-1.jpg') center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;

    .text-secondary {
      font-size: 2rem;
      margin-bottom: 2rem;
    }
  }

  &-items {
    width: 60vw;
    margin: 2rem auto 0 auto;
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 2rem;
  }

  &-item {
    position: relative;
    border-bottom: 5px solid $secondaryColor;
    overflow: hidden;
    cursor: pointer;

    img {
      width: 100%;
    }

    &::after {
      content: '';
      position: absolute;
      top: 100%;
      left: 0;
      height: 100%;
      width: 100%;
      background: $secondaryColor;
      opacity: 0;

      @include transition-ease;
    }

    &:hover {
      &::after {
        top: 0;
        opacity: 0.8;
      }

      .projects-btns-btn {
        opacity: 1;
      }
    }
  }

  &-btns {
    position: absolute;
    top: 0;
    height: 100%;
    width: 100%;
    z-index: 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    text-align: center;

    &-btn {
      opacity: 0;
      color: set-text-color($secondaryColor);

      @include transition-ease;

      &:hover {
        color: set-text-color(set-text-color($secondaryColor));
      }
    }
  }

  .social-icons {
    display: flex;
    flex-direction: column;
  }

  footer {
    transform: rotate(90deg) translate(-3rem, -5rem);
  }
}
