@include media-md {
  .menu-btn {
    visibility: hidden;
  }

  .nav {
    visibility: visible;

    .menu-nav {
      display: block;
      transform: translateY(0);
      height: 100%;
      background: transparent;
      text-align: right;

      &-item {
        display: inline;
        padding-right: 1.5rem;
      }

      &-link {
        font-size: 1.5rem;
      }
    }
  }

  .about-bio {
    font-size: 1.5rem;
  }

  .projects {
    &-bio-image {
      height: 40vh;
    }

    &-items {
      grid-template-columns: repeat(2, 1fr);
    }

    .text-secondary {
      font-size: 3rem;
    }
  }

  .contact-list {
    grid-template-columns: repeat(2, 1fr);
  }

  .contact {
    .social-icons {
      position: absolute;
      margin-bottom: 10%;
      left: 50%;
      transform: translateX(-50%);
      margin-top: 2rem;
    }
  }
}

@include media-lg {
  .projects {
    &-items {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  .contact-list {
    grid-template-columns: repeat(3, 1fr);
  }
}

@include media-xl {
  .projects-bio-image {
    height: 50vh;
  }
}
