.about {
  padding-bottom: 2rem;
  overflow-x: hidden;

  &-bio-image {
    height: 50vh;
    width: 100%;
    background: linear-gradient(
        to right,
        rgba($primaryColor, 0.9),
        rgba($primaryColor, 0.3)
      ),
      url('../assets/model-1.jpg') center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;

    p {
      margin-bottom: 2rem;
    }
  }

  &-bio {
    width: 80%;
    text-align: center;

    .text-secondray {
      padding-bottom: 1rem;
    }
  }

  .jobs {
    width: 60vw;
    margin: 2rem auto 0 auto;
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 2rem;

    &-job {
      background: lighten($primaryColor, 10%);
      padding: 0.5rem 0 0.5rem 0;
      border-bottom: 5px solid $secondaryColor;

      h2,
      h3 {
        margin: 0.5rem 0;
      }
      h6 {
        margin: 0.3rem 0;
      }
    }
  }

  .social-icons {
    display: flex;
    flex-direction: column;
  }

  footer {
    transform: rotate(90deg) translate(-3rem, -5rem);
  }
}
